<template>
    <div class="input-fields">
        <div class="field">
            <label for="balance">Balance</label>
            <input type="number" id="balance" placeholder="USDT" v-model="balance">
        </div>
        <div class="field">
            <label for="interest">Interest Rate</label>
            <input ref="interest" type="number" id="interest" placeholder="%" v-model="interest" max="100">
        </div>
        <div class="field">
            <label for="withdrawal-fee">Withdrawal Fee</label>
            <input ref="withdrawal_fee" type="number" id="withdrawal-fee" placeholder="%" v-model="withdrawal_fee" max="100">
        </div>
        <div class="field">
            <label for="visa">Visa</label>
            <select ref="visa" id="visa" v-model="visa">
                <option value="0" disabled selected hidden>Select Visa</option>
                <option value="4">M1: $4/day</option>
                <option value="14">M2: $14/day</option>
                <option value="39">M3: $39/day</option>
                <option value="64">M4: $64/day</option>
                <option value="112">M5: $112/day</option>
                <option value="132">M6: $132/day</option>
                <option value="151.8">M7: $151.8/day</option>
                <option value="171.6">M8: $171.6/day</option>
            </select>
        </div>
        <div class="field">
            <div>Days (Experimental feature)</div>
            <input ref="start_days" type="number" placeholder="Start" v-model="start_day">
            <input ref="end_days" type="number" placeholder="End" v-model="end_day">
        </div>
        <div class="field">
            <button @click="generate">Calculate</button>
        </div>
    </div>
    <div v-if="result" class="results">
        <table>
            <tr>
                <th>Day</th>
                <th>Prev balance</th>
                <th>Interest</th>
                <th>Cumulative Interest</th>
                <th>Balance</th>
                <th>Flat Earning</th>
                <th>Withdrawal Fee</th>
                <th>Net</th>
                <th>Day</th>
            </tr>
            <tr v-for="row, key in result" :key="key">
                <td class="column-day" :class="{ 'odd-index': row.day % 2 }">{{ row.day }}</td>
                <td class="column-prev-balance" :class="{ 'odd-index': row.day % 2 }">{{ row.prev_balance }}</td>
                <td class="column-interest" :class="{ 'odd-index': row.day % 2 }">{{ row.current_interest }}</td>
                <td class="column-cumulative-interest" :class="{ 'odd-index': row.day % 2 }">{{ row.cumulative_interest }}
                </td>
                <td class="column-balance" :class="{ 'odd-index': row.day % 2 }">{{ row.current_balance }}</td>
                <td
                    :class="{ 'net-income': row.net > row.flat_earning, 'net-loss': row.net < row.flat_earning, 'odd-index': row.day % 2 }">
                    {{
                        row.flat_earning }}</td>
                <td class="column-withdrawal-fee" :class="{ 'odd-index': row.day % 2 }">{{ row.withdrawal_fee }}</td>
                <td
                    :class="{ 'net-income': row.net > row.flat_earning, 'net-loss': row.net < row.flat_earning, 'odd-index': row.day % 2 }">
                    {{ row.net
                    }}</td>
                <td class="column-day" :class="{ 'odd-index': row.day % 2 }">{{ row.day }}</td>
            </tr>
            <tr>
                <th>Day</th>
                <th>Prev balance</th>
                <th>Interest</th>
                <th>Cumulative Interest</th>
                <th>Balance</th>
                <th>Flat Earning</th>
                <th>Withdrawal Fee</th>
                <th>Net</th>
                <th>Day</th>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    name: 'App',
    components: {
    },
    data() {
        return {
            balance: null,
            interest: null,
            withdrawal_fee: null,
            visa: 0,
            start_day: null,
            end_day: null,
            result: null //[
            //     { day: 1, prev_balance: 1, current_interest: 1, cumulative_interest: 1, current_balance: 1, flat_earning: 1, withdrawal_fee: 1, net: 0 },
            //     { day: 2, prev_balance: 1, current_interest: 1, cumulative_interest: 1, current_balance: 1, flat_earning: 1, withdrawal_fee: 1, net: 1 },
            //     { day: 3, prev_balance: 1, current_interest: 1, cumulative_interest: 1, current_balance: 1, flat_earning: 1, withdrawal_fee: 1, net: 2 }
            // ]
        };
    },
    methods: {
        calculate: function (start, end, balance, interest, visa) {
            let res = [];
            let prev_balance = balance;
            let current_interest = 0;
            let cumulative_interest = 0;
            let flat_earning = 0;
            for (let i = start; i <= end; i++) {
                current_interest = (prev_balance * (1 + (interest / 100))) - prev_balance;
                cumulative_interest += current_interest;
                flat_earning = balance + (visa * i);
                res.push({
                    day: i,
                    prev_balance: parseFloat(prev_balance.toFixed(5)),
                    current_interest: parseFloat(current_interest.toFixed(5)),
                    cumulative_interest: parseFloat(cumulative_interest.toFixed(5)),
                    current_balance: parseFloat((flat_earning + cumulative_interest).toFixed(5)),
                    flat_earning: parseFloat(flat_earning.toFixed(5)),
                    withdrawal_fee: parseFloat(((flat_earning + cumulative_interest) * (this.withdrawal_fee / 100)).toFixed(5)),
                    net: parseFloat(((flat_earning + cumulative_interest) * ((100-this.withdrawal_fee) / 100)).toFixed(5))
                });
                prev_balance = flat_earning + cumulative_interest;
            }
            return res;
        },
        generate: function () {
            if (this.$refs.visa.value == 0 || !this.$refs.interest.value || !this.$refs.withdrawal_fee.value) {
                return;
            }
            if (this.interest > 100) {
                this.interest = 100;
            }

            if (this.withdrawal_fee > 100) {
                this.withdrawal_fee = 100;
            }

            if (this.start_day < 0 || !this.start_day) {
                this.start_day = 1;
            }

            if (this.start_day > this.end_day || !this.end_day) {
                this.end_day = this.start_day + 30;
            }

            this.result = this.calculate(this.start_day, this.end_day, this.balance || 0, this.interest || 0, this.visa || 0);
        }
    }
}
</script>

